export default{
	props:{
		noback:{
			type:Boolean,
			default:true
		}
	},
	data(){
		return{
			providerList: [],
		}
	},
	onLoad() {
	    
	},
	methods:{
		async loginEvent(data){//第三方登录
			let [err,res]=await this.user.login({
				url:'/user/otherlogin',
				data:data,
				Noback:this.noback
			})
			console.log(JSON.stringify(res.data.data))
			if(res){
				this.$emit('logining')
			}
		},
		tologin(e) {//第三方登录
			switch (e){
				case 'weixin':
				uni.login({
					provider:'weixin',
					scopes:'auth_user',
					success: (res) => {
						// console.log(JSON.stringify(res))
						uni.getUserInfo({
							provider:'weixin',
							success: (infoRes) => {
								let data = this.user.__formatOtherlogin('weixin',Object.assign(infoRes,res));//改变服务器所需要的数据结构
								
								this.loginEvent(data);
							}
						})
					}
				})
					break;
				case 'qq':
				uni.login({
					provider:'qq',
					scopes:'auth_user',
					success: (res) => {
						// console.log(JSON.stringify(res))
						uni.getUserInfo({
							provider:'qq',
							success: (infoRes) => {
								//获取第三方登录相关信息
								let data = this.user.__formatOtherlogin('qq',Object.assign(infoRes,res));
								//登录
								
								this.loginEvent(data);
							}
						})
					}
				})
					break;
				case 'sinaweibo':
				uni.login({
					provider:'sinaweibo',
					scopes:'auth_user',
					success: (res) => {
						console.log(JSON.stringify(res))
						uni.getUserInfo({
							provider:'sinaweibo',
							success: (infoRes) => {
								// console.log(JSON.stringify(infoRes))
								let data = this.user.__formatOtherlogin('sinaweibo',Object.assign(infoRes,res));
								this.loginEvent(data);
							}
						})
					}
				})
					break;	
			}
		}
	}
}
